
<template>
  <button @click="toggleTheme" aria-label="Theme Switcher">
    <i v-if="currentTheme === 'light'" data-feather="moon"></i> <!-- Moon icon for dark theme -->
    <i v-else data-feather="sun"></i> <!-- Sun icon for light theme -->
  </button>
</template>

<script>
import { onMounted } from 'vue';
import feather from 'feather-icons';
import useThemeSwitcher from '@/composables/useThemeSwitcher'; // Ensure the path is correct

export default {
  setup() {
    const { currentTheme, toggleTheme } = useThemeSwitcher();

    // Call feather.replace() to transform icons when the component mounts
    onMounted(() => {
      feather.replace();
    });

    // Update icons when theme changes
    const updateIcons = () => {
      feather.replace();
    };

    return {
      currentTheme,
      toggleTheme: () => {
        toggleTheme();
        updateIcons(); // Call updateIcons after toggling the theme
      },
    };
  },
};
</script>
